<template>
  <div class="payplugUISelect _show"
       :class="selectDisabled ? '-disabled' : null"
       :id="selectName">
    <div class="_current" tabindex="1">
      <div class="_value" v-for="option in selectOptions" v-bind:key="option">
        <input class="_input"
               type="radio"
               :id="selectName + '-' + option.value"
               :value="option.value"
               :name="selectName"
               :checked="(option?.checked) ? 'checked' : null"
               :disabled="(option?.disabled || selectDisabled) ? 'disabled' : null"
               :data-e2e-name="selectName + '-' + option.value"
               @change="store.storeValue(selectName, $event.target.value)">
        <span class="_text">{{ option.label }}</span>
      </div>
    </div>
    <div class="_listWrapper">
      <div class="_list">
        <ul>
          <li  v-for="option in selectOptions" v-bind:key="'label'+option">
            <label class="_option" :for="selectName + '-' + option.value">{{ option.label }}</label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { store } from "@/store/store";

export default {
    name: "AtomSelect",
    props:{
      selectName: {
        type: String,
        required: true
      },
      selectOptions: {
        type: Object,
        required: true
      },
      selectDisabled: {
        type: Boolean,
        required: false
      }
    },
    data() {
      return {
        store: store
      }
    },
    created() {
		this.populate();
    },
	updated() {
		this.populate();
	},
	methods:{
		populate(){
			let selected = "";
			this.selectOptions.map(option => {
				if(option.checked)
					selected = option.value
			})
			this.store.storeValue(this.selectName, selected)
		}
	}
  }
</script>


