<template>
  <div class="_footer">
    <payplugUIButton
        class="saveChangesButton"
        :buttonText="save_changes_text"
        :disabled="disabled"
        @click="$emit('saveData')"  v-bind:class="{ '-disabled' : disabled}" data-e2e-name="saveConfiguration"/>
    <div class="_faq" v-if="description">
      <Paragraph :paragraphText="description[0]"/>
      <Paragraph :paragraphText="description[1]" :paragraphLinks="link_help" />
    </div>
  </div>
</template>


<script>
import payplugUIButton from "@/components/atoms/AtomButton";
import Paragraph from "@/components/atoms/AtomParagraph";
import {store} from "@/store/store";

export default {
  name: "OrganismSectionFooter",
  components: { payplugUIButton, Paragraph },
  props: {
    disabled: {
      type: Boolean,
      required: true
    }
  },
  computed:{
    save_changes_text: {
      get: () => store.translations.footer.save_changes_text
    },
    description: {
      get: () => store.translations.footer.description
    },
    link_help: {
      get: () => store.translations.footer.link_help
    },
  }
}
</script>