<template>
	<div class="payplugConfiguration">
		<div class="payplug">
			<br/><br/>
			<pre v-if="showStore" class="store" style="z-index:99">{{store.data}}</pre>
			<Modal ref="generalModal" :modal-content="modal_data.msg" :modal-title="modal_data.title" :modal-close="modal_data.close"></Modal>
			<AtomLoader v-if="loading" class="global-wrapper" />
			<div v-bind:class = "(loading)?'blurred':''">
				<Header />
				<Login v-if="!logged && !register" v-on:notRegisted="updateparent" v-on:showLoggedIn="showLoggedIn" />
				<Subscribe v-if="!logged && register" v-on:notRegisted="updateparent" />
				<LoggedIn v-bind:class="{ '-disabled' : plugin_disabled }" v-if="logged" v-on:showLogin="showLogin" />
				<PMethods v-bind:class="{ '-disabled' : !logged || plugin_disabled }" />
				<Paylater v-bind:class="{ '-disabled' : !logged || plugin_disabled }" />
				<Status v-bind:class="{ '-disabled' : (!logged || plugin_disabled) && (!logged || !store.translations.status.error) }" />
        <Foot :disabled="disabled" @saveData="saveData"/>
			</div>
		</div>
	</div>
</template>

<script>
import Header from "@/components/organisms/OrganismSectionHeader";
import Login from "@/components/organisms/OrganismSectionLogin";
import Subscribe from "@/components/organisms/OrganismSectionSubscribe";
import LoggedIn from "@/components/organisms/OrganismSectionLoggedIn";
import Paylater from "@/components/organisms/OrganismSectionPaylater";
import Status from "@/components/organisms/OrganismSectionStatus";
import PMethods from "@/components/organisms/OrganismSectionPMethods";
import AtomLoader from "@/components/atoms/AtomLoader";
import { store } from "@/store/store";
import {$init, $save} from "@/store/ajax";
import Modal from "@/components/molecules/MoleculeModal";
import Foot from "@/components/organisms/OrganismSectionFooter";

export default {
	computed:{
		plugin_disabled: {
			get: () => parseInt(store.data.payplug_enable) != 1
		},
		logged: {
			get: () => store.settings.logged
		},
		loading:{
			get: () => store.settings.loading.global
		},
		disabled: function () {
			if (!this.logged || store.settings.save.disabled === true || store.translations.status.error)
				return true;

			return false;

		},
		modal_data(){
			let modal_data = store.settings.save.data;


			if( modal_data.msg )
				this.showModal();

			return modal_data;
		},
	},
	name: "app",
	components:{ Header, Login, Subscribe, LoggedIn, Status, PMethods, Paylater, AtomLoader, Modal, Foot },
	data(){
		return {
			store: store,
			register: false,
			showStore: false,
		}
	},
	mounted() {
		$init();
	},
	methods: {
		updateparent(status) {
			this.register = status
		},
		showLoggedIn() {
			this.logged = true
		},
		showLogin() {
			this.logged = false
		},
		saveData() {
			$save();
		},
		showModal(){
			this.$refs.generalModal.showModal();
		},
		closeModlal() {
			this.$refs.generalModal.closeModal();
		}
	}
}
</script>

