import { reactive } from 'vue'

export const store = reactive({
    // all data that will be used for the different requests
    settings: {
		logged: false,
		standard_payment_mode: null,
		loading: {
			global:true,
			login:false,
			logout: false,
			status: false,
			payment_methods:{
				bancontact: false,
				applepay: false,
				american_express: false,
			}
		},
		save:{
			disabled : false,
			data: {title:'',msg:'',close:''},
		},
		mode: false,	//false=test || true=live
        "nonce": ""
    },

	//data added on error messages
	error:{},

	// If account inactive & in Live mode
	error_inactive: false,

	// Success message after clicking check
	success_check: false,

	// If account inactive & in Live mode after refresh_keys call
	error_inactive_alert: false,

	// data needed for requests
    data: {
		payplug_sandbox: true //Test Mode = default

	},

    storeValue(name, value, action = "data") {
        this[action][name] = value
    },

	manageMode(){
		this.data.payplug_sandbox = this.settings.mode;

		if(this.settings.mode === true || this.settings.mode === 1 || this.settings.mode === "1"){
			return 'sandbox';
		}

		return 'live';
	},

	translations:{
		header:{},
		login:{},
		subscribe:{},
		logged:{},
		payment_paylater:{},
		payment_methods:{},
		status: {},
		debug: {},
		footer: {}
	},
	rawData:{}
})
