<template>
  <div class="payplugUICheckbox">
    <label class="checkbox-label">
      <div>
        <input type="checkbox" :name="name" :checked="checked" @change="emitChange"/>
        <span>{{ title }}</span>
      </div>

      <Paragraph :paragraphText="description"/>
    </label>
  </div>
</template>

<script>
import Paragraph from "../atoms/AtomParagraph";
import { store } from "@/store/store";

export default {
	name: "MoleculeCheckbox",
	components: {Paragraph},
	computed: {
		title: {
			get: () => store.translations.debug.title
		},
		description: {
			get: () => store.translations.debug.description
		},
		name: {
			get: () => store.translations.debug.name
		},
		checked:{
			get: ()=> store.translations.debug.checked,
			set: function(e){
				return this.store.storeValue(this.name, e.target.checked)
			}
		}
	},
	data() {
		return {
			store: store,
		}
	},
	created() {
		this.store.storeValue(this.name, this.checked || false)
	},
	updated() {
		this.store.storeValue(this.name, this.checked || false)
	},
	methods:{
		emitChange(e) {
			this.store.storeValue(this.name, e.target.checked)
		}
	}
}
</script>

