<template>
  <Accordion :class="'payplugUIAccordion ' + payment_method_name + 'PaymentAdvanced'" :accordionLabel="title"
      :data-e2e-name="payment_method_name + 'AdvancedSettings'">
    <div class="_standardAdvancedOption" v-for="option in options" v-bind:key="option" :class="option.class" >
      <div class="wrapper">
        <div class="_header">
          <Title :titleText="option.title" />
          <Switch :switchName="option.switch_enable?.name" :switchChecked="option.switch_enable?.checked ? true : null" switchData="installmentSwitch"/>
        </div>
        <div class="_content">
          <div v-for="description in option.deferred_descriptions" v-bind:key="description" style="margin-bottom: 8.5px;">
            <Paragraph :paragraphText="description.text" class="d-inline"/>
            <Anchor v-for="anchor in description.links" v-bind:key="anchor"
                    :anchorHref="anchor.url"
                    :anchorText="anchor.text"
                    :anchorTitle="anchor.text"
                    :anchorTarget="anchor.target"  />
          </div>
          <div class="_inputs">
            <Paragraph v-if="option.installements" :paragraphText="option.installements.description" />
            <SelectBox v-if="option.installements"
                       :class="'installmentMode'"
                       :selectName="option.installements.options_installment_name"
                       :selectOptions="option.installements.options_installment"
                       :selectDisabled="option.installements.options_installment_disabled"/>
            <Paragraph v-if="option.installements" :paragraphText="option.installements.text_from" />
            <div v-if="option.installements" class="payplugUIInput installmentMinAmount -icon -iconEuro -disabled">
              <AtomInput
                  :inputType="'number'"
                  :inputName="option.installements.input_amount.name"
                  :id="option.installements.input_amount.name"
                  :inputDisabled="option.installements.options_installment_disabled"
                  :inputValue="option.installements.input_amount.value"
                  :inputMin="option.installements.input_amount.min"
                  :inputMax="option.installements.input_amount.max"
                  :inputStep="option.installements.input_amount.step"
                  :class="input_amount_error ? 'inputError' : null"/>
            </div>
          </div>
          <template v-for="description in option.installements_descriptions" v-bind:key="description">
            <Paragraph :paragraphText="description.text" class="d-inline mb-0"/>
            <Anchor v-for="anchor in description.links" v-bind:key="anchor"
                    :anchorHref="anchor.url"
                    :anchorText="anchor.text"
                    :anchorTitle="anchor.text"
                    :anchorTarget="anchor.target"  />
          </template>
          <Paragraph v-if="option.deferred_description" :paragraphText="option.deferred_description" />
          <SelectBox v-if="option.capture"
                     :selectName="option.capture_name"
                     :selectOptions="option.capture"
                     :selectDisabled="option.capture_disabled" />
          <div class="_statement" v-if="option.installements && input_amount_error">{{option.installements.input_amount_error}}</div>
          <TextAlert v-if="option?.notes" :textAlertText="option?.notes?.description" :textAlertType="option?.notes?.type" />
        </div>
      </div>
    </div>
  </Accordion>
</template>

<script>
import Accordion from "@/components/molecules/MoleculeAccordion";
import Switch from "@/components/atoms/AtomSwitch";
import Title from "@/components/atoms/AtomTitle";
import Paragraph from "@/components/atoms/AtomParagraph";
import SelectBox from "@/components/atoms/AtomSelect";
import AtomInput from "@/components/atoms/AtomInput";
import Anchor from "@/components/atoms/AtomAnchor";
import TextAlert from "@/components/atoms/AtomTextAlert";
import {store} from "@/store/store";

export default  {
  name: "OrganismPMethodAdvanced",
  components:{ Accordion, Switch, Title, Paragraph, SelectBox, AtomInput, Anchor, TextAlert },
  computed:{
    payment_method_name: {
      get: () => store.translations.payment_methods.payment_methods[0].name
    },
    title: {
      get: () => store.translations.payment_methods.payment_methods[0].advanced_settings.title
    },
    options: {
      get: () => store.translations.payment_methods.payment_methods[0].advanced_settings.options
    },
    input_amount_error: {
      get: () => store.data.enable_payplug_inst && ( parseInt(store.data.payplug_inst_min_amount) < 4 || parseInt(store.data.payplug_inst_min_amount) > 20000 )
    },
  },
  created() {
  },
  updated() {
  },
  methods: {
  }
}
</script>

